<template>
  <div class="cancelForm">
    <form action>
      <div class="inputBox">
        <p>해지를 위해서는 가입자 확인이 필요합니다.</p>
        <div class="inputList">
          <div>
            <input type="text" v-model="userName" placeholder="성명(홍길동)" maxlength="3" />
          </div>
          <div>
            <input type="text" v-model="userBirth" placeholder="생년월일(19900101)" maxlength="8" />
          </div>

          <div>
            <input type="text" v-model="userPhone" placeholder="휴대폰번호('-'제외)" maxlength="11" />
          </div>

          <div class="genderSelect">
            <ul>
              <li>
                <span :class="{ active : userGender }" v-on:click="userGender = 1">남</span>
              </li>
              <li>
                <span :class="{active : !userGender}" v-on:click="userGender = 0">여</span>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div class="sendMsg">
        <button type="button" v-on:click="startTimer">인증번호 전송</button>
      </div>

      <div class="confirmInput">
        <div class="inputWrap">
          <input type="text" v-model="keyNumber" maxlength="6" />
          <div class="timer">
            <span>{{ timeValue}}</span>
          </div>
        </div>
      </div>

      <div class="btnArea">
        <div class="leftBox">
          <a href>취소</a>
        </div>
        <div class="rightBtn">
          <button type="submit">PASS세이프가드 해지</button>
        </div>
      </div>
      <div class="infoText">
        <p>* 해지 시 해당월 이용내역에 대해서는 일할 계산되어 청구됩니다.</p>
      </div>
    </form>
  </div>
</template>

<script>
export default {
	data() {
		return {
			keyNumber: '',
			timeValue: '3:00',
			userGender: 1,
			userName: '',
			userBirth: '',
			userPhone: '',
		};
	},
	methods: {
		startTimer(duration) {
			var timer = 60 * 3,
				minutes,
				seconds;
			var vd = this;
			setInterval(function() {
				minutes = parseInt(timer / 60, 10);
				seconds = parseInt(timer % 60, 10);

				minutes = minutes < 10 ? minutes : minutes;
				seconds = seconds < 10 ? '0' + seconds : seconds;

				vd.timeValue = minutes + ':' + seconds;

				if (--timer < 0) {
					timer = duration;
				}
			}, 1000);
		},
	},
};
</script>

